body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.container {
  width: 320px;
  margin: 110px auto 0;
  padding: 0 10px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}

.search-form {
  width: 100%;
  background: linear-gradient(to right, #0f3443, #34e89e);
  justify-content: center;
  align-items: center;
  padding: 25px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.search-form__input {
  width: 180px;
  height: 40px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.search-form__input::-ms-input-placeholder {
  color: #ccc;
  font-size: 16px;
}

.search-form__input::placeholder {
  color: #ccc;
  font-size: 16px;
}

@media screen and (min-width: 768px) {
  .search-form__input {
    width: 400px;
  }
}

@media screen and (min-width: 1440px) {
  .search-form__input {
    width: 700px;
  }
}

.search-form__button {
  width: 100px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
}

.search-form__button:hover, .search-form__button:focus {
  color: #fff;
  background-color: #0f3443;
}

@media screen and (min-width: 320px) {
  .search-form__button {
    width: 70px;
  }
}

@media screen and (min-width: 768px) {
  .gallery {
    flex-wrap: wrap;
    margin: -10px;
    display: flex;
  }
}

.photo-card {
  width: 300px;
  border-radius: 5px;
  margin-bottom: 10px;
  overflow: hidden;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .5);
}

.photo-card :last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .photo-card {
    width: calc(50% - 20px);
    margin: 10px;
  }
}

@media screen and (min-width: 1440px) {
  .photo-card {
    width: calc(25% - 20px);
  }
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.load-more {
  width: 200px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: 50%;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #0f3443;
}

.load-more.is-hidden {
